.icons {
  display: flex;
  flex-direction: row;
  justify-content: space-around; }
  .icons a {
    background: rgba(38, 38, 38, 0.7);
    padding: 10px;
    border-radius: 5px;
    margin-right: 10px; }
  .icons i {
    color: white;
    border-radius: 5px;
    font-size: 40px;
    transition: all 0.1s ease-out; }
    .icons i:hover {
      cursor: pointer; }

.header-component {
  text-align: center;
  font-family: 'Roboto', sans-serif;
  width: 80%;
  padding: 40px 40px;
  height: 20%;
  align-self: center; }
  @media only screen and (max-width: 425px) {
    .header-component {
      width: 100%;
      padding: 40px 20px; } }
  .header-component .header-list {
    display: flex;
    justify-content: space-between; }
    .header-component .header-list .header-left-side {
      font-weight: bold; }
    .header-component .header-list .header-item {
      color: white;
      margin-left: 1rem;
      display: inline-block;
      position: relative; }
      .header-component .header-list .header-item:first-child {
        margin-left: 0; }
      .header-component .header-list .header-item::after {
        content: '';
        display: block;
        margin: auto;
        height: 3px;
        width: 0;
        background: transparent;
        transition: width .5s ease; }
      .header-component .header-list .header-item:hover {
        cursor: pointer; }
        .header-component .header-list .header-item:hover::after {
          width: 100%;
          background: #CF5050; }
    .header-component .header-list a {
      margin-left: 1rem; }

.about-me {
  background-size: cover;
  height: 80%;
  display: flex;
  align-items: center;
  justify-content: center; }
  .about-me .my-name {
    padding-left: 10px;
    padding-right: 10px;
    background: yellow;
    color: black;
    border: 1px solid black;
    border-radius: 3px; }
  .about-me div {
    background: #262626b3;
    height: auto;
    width: 50vw;
    padding: 2rem;
    font-family: 'Nunito', sans-serif; }
    @media only screen and (max-width: 425px) {
      .about-me div {
        width: 100vw;
        margin: 0 20px; } }
    .about-me div h4 {
      -webkit-background-clip: text;
      margin-bottom: 4%;
      text-align: center;
      color: #CF5050; }
      .about-me div h4 .curly-braces {
        font-weight: bold;
        color: white; }
    .about-me div p {
      color: white;
      text-align: left; }
    .about-me div iframe {
      margin-left: auto;
      display: block;
      margin-right: auto; }

.header-component .header-list .header-item {
  color: black; }

.projects {
  height: 80%;
  display: flex;
  flex-direction: column;
  font-family: 'Roboto', sans-serif;
  width: 100%;
  margin: 0 auto;
  max-width: 800px;
  padding: 0 40px; }
  .projects .project {
    border-bottom: 1px solid #e2e8f0;
    padding: 1.45rem 0; }
    .projects .project .project-title {
      display: flex;
      flex-direction: row; }
      .projects .project .project-title > a {
        margin-right: 1rem; }
      .projects .project .project-title .project-name-link .project-name {
        font-weight: bold;
        font-size: 1.1rem;
        color: black;
        position: relative;
        width: -webkit-max-content;
        width: -moz-max-content;
        width: max-content;
        margin-bottom: 0; }
        .projects .project .project-title .project-name-link .project-name:hover:after {
          content: '';
          position: absolute;
          margin-top: 3px;
          left: 0;
          right: 0;
          bottom: 0;
          height: 3px;
          background: #000; }
      .projects .project .project-title .project-name-link:hover {
        text-decoration: none; }
      .projects .project .project-title .project-live-link {
        color: black;
        text-decoration: none;
        position: relative; }
        .projects .project .project-title .project-live-link .live-link-text {
          margin-bottom: 0; }
          .projects .project .project-title .project-live-link .live-link-text:hover:after {
            content: '';
            position: absolute;
            margin-top: 3px;
            left: 0;
            right: 0;
            bottom: 0;
            height: 3px;
            background: #000; }
    .projects .project .project-detail {
      color: #4a5568;
      font-size: 0.7975rem; }

.info {
  width: 100%;
  margin: 0 auto;
  max-width: 800px;
  padding: 20px 40px; }
  .info p {
    color: #718096; }

